.affairs {
    background: linear-gradient(90deg, #4676d7, cadetblue);
    width: 15%;
    margin-left: 5%;
    border-radius: 5px;
}

.affairs:hover {
    background: linear-gradient(90deg, cadetblue, #4676d7);
}

.affair {
    display: flex;
    justify-content: space-between;
    margin: 2%;
}

.delAffair:hover {
    background: linear-gradient(90deg, red, orangered);
    -webkit-transform: scale(0.7);
    -ms-transform: scale(0.7);
    transform: scale(0.7);
}

.buttons {
    padding: 5%;
    margin: 1%;
}

@keyframes bounce {
    0%, 20%, 60%, 100% {
        transform: translateY(0);
        transform: translateY(0);
    }

    40% {
        transform: translateY(-20px);
        transform: translateY(-20px);
    }

    80% {
        transform: translateY(-10px);
        transform: translateY(-10px);
    }
}

.buttons button:hover {
    animation: bounce 1s;
    background: linear-gradient(90deg, #61dafb, #4676d7);
    margin: -1%;
}



